<template>
  <div class="modal-document">
    <h4 class="fw-bold"> Nim do walki ruszył 2 Korpus</h4>
    <p class="fw-bold">
      Monte Cassino, Ankona, Bolonia – te nazwy najczęściej są wymieniane, gdy mówi się o dokonaniach oręża polskiego na
      froncie włoskim II wojny światowej. Oczywiście pada przy tym nazwa 2 Korpusu Polskiego, do którego te zwycięstwa
      należą. Żołnierze generała Władysława Andersa nie byli jednak pierwszymi w Italii, przed nimi imię Polski
      rozsławili tam nasi marynarze, lotnicy, partyzanci i komandosi.
    </p>
    <p>
      Kiedy alianci rozgromili wojska włoskie i niemieckie w Afryce Północnej, latem 1943 roku zwrócili się ku Sycylii.
      Zdobycie tej wyspy gwarantowało panowanie nad całym Morzem Śródziemnym i dawało możliwość inwazji na Półwysep
      Apeniński. W armadzie inwazyjnej nie zabrakło statków i okrętów pod polską banderą. Zresztą marynarze z polskiej
      marynarki wojennej i handlowej poznali szlaki wodne wokół Italii już wcześniej, gdy pływali w konwojach na Maltę i
      do Afryki. Operacja „Husky”, czyli desant na Sycylię, okazała się szczęśliwa dla polskich marynarzy. Transportowce
      MS „Batory”, MS „Sobieski” i MS „Kościuszko”, wypełnione wojskiem oraz sprzętem, bez strat dotarły do wybrzeży
      Sycylii. Równie dobrze wiodło się niszczycielom – ORP „Krakowiakowi”, ORP „Piorunowi” i ORP „Ślązakowi”, które
      ubezpieczały desanty ogniem dział, a także odpierały ataki nieprzyjacielskiego lotnictwa.
      Znacznie cięższe dla polskiej floty było lądowanie sprzymierzonych na wybrzeżu Włoch. Między innymi 3 grudnia 1943
      roku na zajęty już przez aliantów port w Bari Luftwaffe dokonało masowego nalotu. Niemieckie bomby zatopiły wtedy
      kilkanaście transportowców, w tym polskie MS „Lwów” i MS „Puck”. Znacznie szczęśliwszy okazał się desant pod Anzio
      i Nettuno, gdzie pośród 250 jednostek znalazły się „Batory”, „Sobieski” i „Narwik”. Polskie niszczyciele zasłużyły
      się podczas działań w zatoce Salerno. „Ślązak” przewodził eskadrze alianckich niszczycieli i odznaczył się podczas
      ratowania załogi zatopionego brytyjskiego pancernika HMS „Warspite”, a „Piorun” zestrzelił dwa niemieckie
      samoloty.
    </p>
    <h4 class="fw-bold"> „Straszne bliźniaki”</h4>
    <p>
      Największą sławę na Morzu Śródziemnym zdobyli jednak polscy podwodniacy. Operowały tutaj dwa bliźniacze okręty –
      ORP „Dzik” i ORP „Sokół”. Obydwa szerzyły wśród włoskiej i niemieckiej marynarki prawdziwe spustoszenie. Pierwszy
      pulę zatopień nieprzyjacielskich jednostek otworzył „Dzik” pod dowództwem komandora Bolesława Romanowskiego,
      skierowany na Morze Śródziemne w maju 1943 roku. Rychło tutaj zaczął odnosić sukcesy. Najpierw storpedował w
      Cieśninie Mesyńskiej liczący 12 tysięcy ton tankowiec, następnie w rejonie Wysp Eolskich czterotysięczny
      transportowiec, a w pobliżu portu Bari dwa pięciotysięczniki. Największy triumf załoga „Dzika” odniosła 21
      września 1943 roku na wodach Korsyki. Tego jednego dnia polscy podwodniacy zatopili 11 niemieckich jednostek
      morskich o łącznej pojemności 14 tysięcy ton. Całkowicie sparaliżowali tym ewakuację morską Niemców z tej wyspy.
      Nie mniejsze sukcesy w tym czasie odnosił bliźniaczy „Sokół” kapitana Jerzego Koziołkowskiego. „Sokół” jako
      pierwszy aliancki okręt wpłynął do portu wojennego włoskiej marynarki wojennej w Brindisi, skąd konwojował na
      Maltę kapitulujące okręty włoskie i transportowce z włoskimi jeńcami. Wykonawszy to zadanie, udał się na patrol
      bojowy na Adriatyk, gdzie w październiku 1943 roku posłał na dno dwa nieprzyjacielskie statki, a trzeci poważnie
      uszkodził. W listopadzie dołączył do „Dzika” na Morzu Egejskim i kapitan Koziołkowski w dzienniku pokładowym
      zapisał kolejnych siedem zatopień. Dzięki tym zwycięstwom obu okrętom Brytyjczycy nadali miano „Terrible twins” –
      „Straszne bliźniaki”. Kiedy „Dzik” i „Sokół” opuszczały basen Morza Śródziemnego, brytyjska admiralicja żegnała
      ich załogi takimi słowami: „Czyny Wasze stały się dla nas natchnieniem, wzbudziły w nas podziw i zaufanie do Was
      po wszystkie czasy”.
    </p>
    <h4 class="fw-bold">Skalski i inni</h4>
    <p>
      W 1943 roku do marynarzy walczących u wybrzeży Włoch dołączyli pierwsi polscy lotnicy – członkowie słynnego
      Polskiego Zespołu Myśliwskiego, bardziej znanego jako „Cyrk Skalskiego”, jak nazwali go sojusznicy od nazwiska
      jego dowódcy, kapitana pilota Stanisława Skalskiego. Po sukcesach „Cyrku” w Tunezji dowództwo RAF zaczęło
      zabiegać, by polscy lotnicy wzięli udział w szykowanej kampanii włoskiej. Każdemu polskiemu oficerowi zaoferowano
      awans i dowodzenie eskadrą, a nawet dywizjonem. Przystali na to Stanisław Skalski, Eugeniusz Horbaczewski i Maciej
      Derecki. Zgodnie z obietnicami wszystkich awansowano. Stanisław Skalski w stopniu majora dowodził angielskim 601
      Dywizjonem Myśliwskim „Country of London”, kapitan pilot Eugeniusz Horbaczewski, zwany „Dziubkiem”, stanął na
      czele 43 Dywizjonu Myśliwskiego, a kapitan pilot Maciej Derecki – na czele eskadry w 152 Dywizjonie Myśliwskim.
      Wszystkie te dywizjony wspierały inwazję na Sycylię i walki armii alianckich w południowych Włoszech. We wrześniu
      1943 roku w czasie startu do kolejnego lotu bojowego zginął kapitan Derecki, a Skalski i Horbaczewski wrócili do
      polskich dywizjonów myśliwskich.
      Na początku 1944 roku na Półwyspie Apenińskim pojawiły się dwie polskie formacje powietrzne: 318 Dywizjon
      Myśliwsko-Rozpoznawczy i 301 Dywizjon Bombowy im. Obrońców Warszawy. Piloci tego pierwszego współdziałali najpierw
      z brytyjską 8 Armią, a następnie 2 Korpusem Polskim. Powietrzni zwiadowcy rozpoznawali i fotografowali cele
      naziemne oraz korygowali ogień artylerii podczas natarć na linię Gustawa. Były to zadania niezwykle niebezpieczne,
      gdyż Niemcy skoncentrowali na tym terenie około 70 ciężkich dział przeciwlotniczych, nie licząc mniejszej broni
      służącej do zwalczania samolotów. Po przełamaniu linii Gustawa 318 Dywizjon wspierał 2 Korpus na całym jego szlaku
      bojowym.
      Natomiast 301 Dywizjon Bombowy, po tym jak w listopadzie znalazł się na włoskim lotnisku w Brindisi, został
      przemianowany na 1856 Eskadrę Specjalnego Przeznaczenia i podporządkowany 334 Skrzydłu Specjalnego Przeznaczenia.
      Odtąd polskie Halifaxy i Liberatory latały ze spadochroniarzami i zaopatrzeniem w celu wsparcia ruchu oporu w
      Albanii, Grecji, Jugosławii oraz w innych krajach okupowanej Europy, w tym w Polsce. Po wybuchu Powstania
      Warszawskiego polscy lotnicy, ponownie pod szyldem 301 Dywizjonu Bombowego, wykonali z Brindisi do Warszawy 80
      lotów z zaopatrzeniem dla powstańców.
    </p>
    <h4 class="fw-bold"> Partyzanci i specjalsi</h4>
    <p>
      Przez cały 1944 rok lotnicy 301 Dywizjonu wspierali także włoską partyzantkę, która w listopadzie 1943 roku
      liczyła już około 30 tysięcy ludzi. Nie brakło w niej Polaków, najczęściej dezerterów z Wehrmachtu. Według różnych
      szacunków we włoskich oddziałach partyzanckich walczyło od pół tysiąca do tysiąca Polaków. We Włoszech znaleźli
      się również polscy cichociemni, wysłani tam do akcji dywersyjnych przez brytyjską Special Operations Executive
      (SOE – Kierownictwo Operacji Specjalnych). Wśród spadochroniarzy walczących na tyłach frontu włoskiego znaleźli
      się między innymi kapitan Władysław Buryn, kapitan Roman Kowalik, porucznik Henryk Śreniawa-Saganowski i sierżant
      Tadeusz Brogowski.
      2 grudnia 1943 roku we włoskim porcie Tarent wylądowała 1 Samodzielna Kompania Commando majora Władysława
      Smrokowskiego. Ten elitarny oddział Polskich Sił Zbrojnych na Zachodzie, szkolony w ośrodkach treningowych
      brytyjskich komandosów, wcielono do 2 Special Service Brigade. 13 grudnia polscy komandosi znaleźli się w Castel
      del Giudice nad rzeką Sangro, gdzie prowadzili rozpoznanie na rzecz brytyjskiej 78 Dywizji Piechoty. Przed samymi
      świętami Bożego Narodzenia osiemdziesięcioosobową kompanię wysłano do położonej na odludziu wioski Pescopennataro,
      skąd miała patrolować górski odcinek frontu. 21 grudnia kwaterę Polaków zaatakowało blisko 300 niemieckich
      strzelców alpejskich. Komandosi jednak nie dali się zaskoczyć i odparli atak – trupem położyli 20 Niemców i kilku
      wzięli do niewoli. Straty własne – tylko trzech rannych.
      W styczniu 1944 roku komandosi znaleźli się nad rzeką Garigliano, aby działać na rzecz 56 Londyńskiej Dywizji
      Piechoty, związanej ciężkimi walkami z Niemcami. Polacy ruszyli do walki wieczorem 17 stycznia w ślad za
      nacierającymi żołnierzami brytyjskimi. Zapiski z tego dnia w dzienniku bojowym oddziału: „Po przekroczeniu rzeki
      na polu minowym kompania ponosi straty: 4 ludzi rannych. Ponieważ czołowy baon 56 dywizji nie uchwycił do godz.
      03.30 dnia 18 stycznia nakazanych punktów przyczółka i nie opanował wzgórz za rzeką, kompania wyruszyła do
      natarcia i o godz. 04.30 opanowuje wzgórze, gdzie po dwóch godzinach zostaje zluzowana przez baon piechoty”. Po
      tym sukcesie komandosi nie poszli na zasłużony odpoczynek, lecz pierwszy pluton uderzył na kolejne wzgórze
      nieopodal wsi Sujo i zniszczył tam gniazdo nieprzyjacielskich moździerzy. W tym samym czasie drugi patrol pod
      dowództwem rotmistrza Stanisława Wołoszowskiego, zastępcy majora Smrokowskiego, zdobył wzgórze Monte Valle
      Martina. Komandosi wyrzucili stamtąd strzelców alpejskich i odparli ich kontratak. Zabili 30 z nich. Niestety, w
      tej walce poległ rotmistrz Wołoszowski.
      Po walkach nad Garigliano kompanię polskich komandosów wyłączono spod dowództwa brytyjskiego. We Włoszech
      wylądował właśnie 2 Korpus Polski. Gdy jego dywizje przygotowywały się do zajęcia pozycji pod Monte Cassino,
      dołączyli również do nich komandosi majora Smrokowskiego. Żołnierze w zielonych beretach Commando rozpoczęli
      kolejny, równie chwalebny, rozdział swej bojowej służby.
    </p>
